import { CONSTANTS } from "../../constants";
import { KyberServer } from "../../api/models";
import { Side } from "../../models";
import "./styles.scss"

type ModalProps = {
    modalOpen: boolean;
    closeModal?: any;
    joinServer?: any;
    modalServer?: KyberServer;
}

const Modal = ({ modalOpen, closeModal, modalServer, joinServer }: ModalProps) => {
    const className = `modal${modalOpen ? " open" : ""}`;

    return (
        <div className={className}>
            <div>
                <div>{CONSTANTS.KYBER.JOIN_SERVER_MESSAGE}</div>
            </div>

            <div className="">
                {!modalServer?.autoBalanceTeams && (
                    <div>
                        <span>Choose faction: </span>
                        <input type="number" id="faction" name="faction" min={Side.Light} max={Side.Dark} required />
                    </div>
                )}
                {modalServer?.requiresPassword && (
                    <div>
                        <span>Password: </span>
                        <input className="" id="password" name="password" type="password" placeholder="Type password here" required />
                    </div>                    
                )}
            </div>

            <div>
                <div className="server-info-server-mods" style={{maxWidth: "500px"}}>
                    <span>
                        {modalServer?.mods?.length === 0
                            ? (<div style={{ color: "#0d3" }}>{CONSTANTS.KYBER.NO_MODS_WARNING}</div>)
                            : (<>
                                    <div>{CONSTANTS.KYBER.MODS_WARNING}</div>
                                    <div className="server-mods-details-content">
                                        {modalServer?.mods?.map((mod, index) => (<div key={`${index}_${mod}`}>{mod}</div>))}
                                    </div>
                               </>
                            )}
                    </span>
                </div>
            </div>
            <div style={{margin: "0 auto"}}>
                {/* TODO: replace eval() with the form */}
                <button className="submit-button" onClick={() => joinServer(modalServer?.id, +eval('window.document.getElementById("faction")?.value'), eval('window.document.getElementById("password")?.value'))}>{CONSTANTS.BUTTON.JOIN}</button>
                <button className="submit-button" onClick={closeModal}>{CONSTANTS.BUTTON.CLOSE}</button>
            </div>
        </div>
    )
}

export default Modal;